import React from 'react'
import {NavLink,Switch,Redirect,  withRouter} from 'react-router-dom'
import {RouteWithSubRoutes} from '../App';
import '../CSS/doc.css'
const Doc = ({routes}) => {
    return (
		<div className='doc'>
			<div className="content">
				<Switch>
					{routes.map((route, i) => (
					<RouteWithSubRoutes key={i} {...route} />
					))}
				</Switch>
				<Redirect from='/doc' to='/doc/core' />
			</div>
			<ol>
				<li>
					<NavLink activeClassName={'left-blue'}  to="/doc/core">核心概念</NavLink>
				</li>
				<li>
					<NavLink activeClassName={'left-blue'}  to="/doc/guide">高级指引</NavLink>
				</li>
				<li>
					<NavLink activeClassName={'left-blue'}  to="/doc/api">API</NavLink>
				</li>
				<li>
					<NavLink activeClassName={'left-blue'}  to="/doc/hooks">Hooks</NavLink>
				</li>
			</ol>
    </div>
	)
}
function Core() {
	return <h1>core</h1>;
}
  
function Guide() {
	return <h1>guide</h1>;
}
  
function Api() {
	return <h1>api</h1>;
}
  
function Hooks() {
	return <h1>hooks</h1>;
}

export {Core, Guide, Api, Hooks}
export default withRouter(Doc)