import Home from './compoent/Home';
import Doc from './compoent/Doc';
import Tuto from './compoent/Tuto';
import Blog from './compoent/Blog';
import Comm from './compoent/Comm';
import Login from './compoent/Login';
import {Core, Guide, Api, Hooks} from './compoent/Doc';

const routes = [
    {
        path: '/',
        exact: true,
        component: Home
    },
    {
        path: '/doc',
        exact: false,
        component: Doc,
        pri: true,
        routes: [
            {
                path:'/doc/core',
                component: Core,
            },
            {
                path: '/doc/guide',
                component: Guide,
            },
            {
                path: '/doc/api',
                component: Api,
            },
            {
                path: '/doc/hooks',
                component: Hooks,
            },
        ]
    },
    {
        path: '/tuto',
        exact: false,
        component: Tuto,
        pri: true
    },
    {
        path: '/blog',
        exact: false,
        component: Blog
    },
    {
        path: '/comm',
        exact: false,
        component: Comm
    },
    {
        path: '/login',
        exact: false,
        component: Login
    },
]


export default routes